@font-face {
  font-family: Roboto;
}

html {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0;
}

body {
  background-color: #f5f5f5;
  margin: 0;
  font-family: Roboto, serif;
}

:root {
  --blue-color: #00acdc;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.logo img {
  height: 40px;
  margin-left: 50px;
  display: block;
}

.navigation {
  justify-content: space-around;
  align-items: center;
  margin-right: 50px;
  display: flex;
}

.nav-list {
  white-space: nowrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: flex;
  overflow: hidden;
}

li {
  justify-content: center;
  align-items: center;
  width: auto;
  height: 60px;
  display: flex;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  justify-content: center;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-link:hover, .nav-link.is-active {
  color: var(--blue-color);
}

.nav-link.is-active:after {
  background-color: var(--blue-color);
  content: "";
  border: 0;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
